/* --------------------------------------------------------------
# Gallery
-------------------------------------------------------------- */
.small-gutters {
  margin-right: -5px;
  margin-left: -5px;

  & > .col,
  & > [class*="col-"] {
    padding-right: 5px;
    padding-left: 5px;
  }
}

.gallery-gutters {
  margin: -15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -ms-flex-wrap: wrap;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;

  & > .col,
  & > [class*="col-"] {
    padding: 15px;
  }
}

.small-gallery-gutters {
  margin: -5px;

  & > .col,
  & > [class*="col-"] {
    padding: 5px;
  }
}

.xs-gallery-gutters {
  margin: -2.5px;

  & > .col,
  & > [class*="col-"] {
    padding: 2.5px;
  }
}



