/* --------------------------------------------------------------
# Search
-------------------------------------------------------------- */
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.openBtn {
  border: none;
  cursor: pointer;
}

.overlay {
  height: 100%;
  width: 100%;
  display: none;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.9);

  .closebtn {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
    cursor: pointer;
    color: white;

    &:hover {
      color: #ccc;
    }
  }

  input[type="text"] {
    padding: 15px;
    font-size: 17px;
    border: none;
    float: left;
    width: 80%;
    background: white;

    &:hover {
      background: #f1f1f1;
    }
  }

  button {
    float: left;
    width: 20%;
    padding: 15px;
    background: #ebf0dc;
    font-size: 17px;
    border: none;
    cursor: pointer;

    &:hover {
      background: #bbb;
    }
  }
}

.overlay-content {
  position: relative;
  top: 46%;
  width: 80%;
  text-align: center;
  margin: auto;
}



