/* --------------------------------------------------------------
# Base styles
-------------------------------------------------------------- */
body {
  font-family: "Roboto", sans-serif !important;
  font-size: 16px !important;
}

img {
  max-width: 100%;
  height: auto;
}

.large-gutters {
  @media (min-width: 576px) {
    margin-right: -25px;
    margin-left: -25px;

    & > .col,
    & > [class*="col-"] {
      padding-right: 25px;
      padding-left: 25px;
    }
  }
}

.xl-gutters {
  @media (min-width: 576px) {
    margin-right: -60px;
    margin-left: -60px;

    & > .col,
    & > [class*="col-"] {
      padding-right: 60px;
      padding-left: 60px;
    }
  }
}

.wp-caption-text {
  font-size: 0.875rem;
}

.z-index-back {
  z-index: 1;
}

.z-index-flower {
  z-index: -1;
}

.z-index-2 {
  z-index: 2;
}

.z-index-3 {
  z-index: 3;
}

.z-index-4 {
  z-index: 4;
}

.image-z-index {
  z-index: -2;
}

.object-fit-cover {
  -o-object-fit: cover;
  object-fit: cover;
}

.letter-space {
  letter-spacing: 3px;
}

.hover-color {
  &:hover {
    color: #747474;
  }
}

.mt-130 {
  margin-top: -130px !important;
}

.display-block {
  display: block;
}

input:-internal-autofill-selected {
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

input:-webkit-autofill-hover {
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

.testimonials-negative-margin {
  @media (min-width: 992px) {
    margin-top: -55px;
    margin-bottom: -55px;
  }
}

.position-md-absolute {
  @media (min-width: 768px) {
    position: absolute;
  }
}

.navbar-icon-size {
  font-weight: 700 !important;
}

#breadcrumbs {
  margin-bottom: 20px;
}

.sep {
  width: 6px;
  height: 10px;
  background-image: url(data:image/svg+xml;charset=utf8,%3Csvg%20width%3D%2211%22%20height%3D%2220%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cpath%20d%3D%22M.38%2019.01c.49.49%201.28.49%201.77%200l8.31-8.31a.996.996%200%20000-1.41L2.15.98a1.25%201.25%200%2000-1.77%200%201.25%201.25%200%20000%201.77L7.62%2010%20.37%2017.25c-.48.48-.48%201.28.01%201.76z%22%20fill%3D%22%232A5631%22%2F%3E%0A%3C%2Fsvg%3E);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: inline-block;
  margin: 0 5px;
}

.search {
  &__input-wrap {
    position: relative;

    input[type="text"] {
      height: 52px;
      border: 2px solid #000;
      font-size: 1rem;
      line-height: 1.375rem;
      font-family: "Fieldwork", serif;
      font-weight: 400;
      letter-spacing: 0.22px;
      outline: none;
      width: 100%;
      background: transparent;
    }

    .search__button {
      position: absolute;
      right: 18px;
      top: 50%;
      transform: translateY(-50%);
      width: 18px;
      height: 19px;
      mask: url("../images/search-icon.svg") 0 0/18px 19px;
      mask-repeat: no-repeat;
      border: none;
      background: #000;
    }
  }
}

.margin-30-0 {
  margin: 30px auto;
}

.single-team__info {
  text-align: center;
}

.img-100px {
  width: 100px;
  height: 100px;
  object-fit: cover;
}

.mb-px-60 {
  margin-bottom: 60px;

  @media screen and (max-width: 640px) {
    margin-bottom: 0;
  }
}



